.single-content-area {
    padding: 15px;
    .post-single {
        .post-head {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            .head-left {
                .article-headline {
                    margin: 0;
                    margin-bottom: 15px;
                }
                .post-meta-data {
                    display: flex;
                    flex-direction: row;
                    p {
                        &:last-child {
                            margin-left: 5px;
                        }
                    }
                }
                .post-meta-list {
                    margin: 0;
                    display: flex;
                    flex-direction: column;
                    .list {
                        display: flex;
                        flex-direction: row;
                        ul {
                            margin-left: 10px;
                            padding: 0;
                            list-style: none;
                            display: flex;
                            flex-direction: row;
                            margin-bottom: 0;
                            a {
                                transition: all .2s ease-in-out;
                                color: #888;
                                font-style: italic;
                                text-decoration: none;
                                li {
                                    &::after {
                                        content: '-';
                                        margin: 0 5px;
                                    }
                                }
                                &:last-child {
                                    li {
                                        &::after {
                                            display: none;
                                        }
                                    }
                                }
                                &:hover {
                                    color: $hover-color;
                                }
                            }
                        }
                        p {
                            margin: 0;
                        }
                        span {
                            font-style: italic;
                            margin: 0;
                            margin-left: 10px;
                            color: #888;
                        }
                    }
                }
                @include media('<=991px') {
                    margin-bottom: 30px;
                }
            }
            .head-right {
                display: flex;
                justify-content: flex-end;
                .post-image {
                    border-radius: 5px;
                    height: 100%;
                    min-height: 175px;
                    width: 500px;
                }
                .download-icon {
                    color: $hover-color;
                    align-self: flex-start;
                    font-size: 80px;
                }
            }
        }
        .content {
            margin-top: 30px;
            margin-bottom: 30px;
            .content-text {
                margin-top: 15px;
            }
            .slider-single-container {
                padding: 15px 0;
                width: 100%;
                .slick-list {
                    overflow: hidden;
                    .slick-track {
                        display: flex;
                        .slider-item {
                            margin: 0 15px;
                            width: 100%;
                            height: 500px;
                            img {
                                width: 100%;
                                height: 600px;
                                object-fit: cover;
                            }
                        }
                    }
                }
                .slick-dots {
                    position: relative;
                }
            }
        }
    }
}